import { Component, OnInit } from '@angular/core';
import{ WebsocketService } from '../websocket.service';

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.css']
})
export class NavigationComponent implements OnInit {

  time: string;
  date: string;
  
  constructor(private websocketService: WebsocketService) { }

  subscribeStatistics(staticsListener: string) {
    this.websocketService.subscribeStatistics(staticsListener);
  }

  ngOnInit() {
    this.websocketService.timeNow.subscribe(
      timeNow => {this.time = timeNow}
    )

    this.websocketService.dateNow.subscribe(
      dateNow => {this.date = dateNow}
    )
  }

}