export class StatisticsData {
    time: date[];
    data: DataClass[];
}

class DataClass {
    legendName: string;
    data: number[];
}

class date {
    year: number;
    month: number;
    day: number;
    hour: number;
    min: number;
    sec: number;
}


export function convertData2GoogleData(inputData: StatisticsData) {
    let chartsData: number[][] = [];
    let legendName: string[] = [];
    let iTime: number;
    let iData: number;
    for (iTime = 0; iTime < inputData.time.length; iTime++) {
      let timeStamp: any[] = [];
      let time = inputData.time[iTime]
      timeStamp.push(new Date(time.year, time.month - 1, time.day, time.hour, time.min, time.sec)); // month: [0, 11]
      for (iData = 0; iData < inputData.data.length; iData++) {
        timeStamp.push(inputData.data[iData].data[iTime]);
      }
      chartsData.push(timeStamp);
    }
    for (iData = 0; iData < inputData.data.length; iData++) {
      legendName.push(inputData.data[iData].legendName);
    }
    return { legendName, chartsData };
  }