import { Injectable } from '@angular/core';
import * as io from 'socket.io-client';
import { Observable, of, Observer, observable } from 'rxjs';
import * as Rx from 'rxjs/Rx';
import { environment } from '../environments/environment';
import { TemperatureSensor } from './temperatureSensor';
import { Subject } from 'rxjs';
import { Device } from './device';
import {StatisticsData} from './statisticsData';


@Injectable({
  providedIn: 'root'
})
export class WebsocketService {

  private socket;
  temperatureSensors = new Subject<TemperatureSensor[]>();
  devices = new Subject<Device[]>();
  timeNow = new Subject<string>();
  dateNow = new Subject<string>();
  timeOfMeasurements = new Subject<string>();
  dateOfMeasurements = new Subject<string>();
  temperaturStatistics = new Subject<StatisticsData>();
  deviceStatistics = new Subject<StatisticsData>();
  statisticsListener: string = "15min";
  test = 1;

  constructor() { }

  connect(): void {
    let self = this // ToDo: Optimize
    this.socket = io(location.protocol + '//' + document.domain + ':' + location.port);
    this.socket.on('sendStatusUpdate', function (message){
      let data = JSON.parse(message);
      self.temperatureSensors.next(data.temperatureSensors);
      self.devices.next(data.devices);
      self.timeOfMeasurements.next(data.timeOfMeasurements);
      self.dateOfMeasurements.next(data.dateOfMeasurements);
      self.timeNow.next(data.time);
      self.dateNow.next(data.date);
   });

    this.socket.on('sendSettings', function (message) {
      console.log("ToDo")
      // ToDo
    });

    this.socket.on('sendStatistics', function (temperaturDataMsg, deviceDataMsg) {
      let temperaturData = JSON.parse(temperaturDataMsg);
      let deviceData = JSON.parse(deviceDataMsg);
      self.temperaturStatistics.next(temperaturData);
      self.deviceStatistics.next(deviceData);
    });
  };

  subscribeStatitics(): void {
    this.socket.emit('subscribeStatistics', {room: this.statisticsListener});
  }

  subscribeStatistics(newStaticsListener: string): void {
    this.statisticsListener = newStaticsListener;
    this.subscribeStatitics();
  };

  disconnect(): void {
    this.socket.disconnect();
    console.log('Disonnected');
  };
}
