import { Component, OnInit } from '@angular/core';
import { Device } from '../device';
import { TemperatureSensor } from '../temperatureSensor';
import { WebsocketService } from '../websocket.service';

@Component({
  selector: 'app-status',
  templateUrl: './status.component.html',
  styleUrls: ['./status.component.css']
})
export class StatusComponent implements OnInit {
  devices: Device[];
  temperatureSensors: TemperatureSensor[];
  date: string;
  time: string;

  getDeviceStyle(state: string) {
    if (state == "off") {
      return 'status_farbe_red';
    } else {
      return 'status_farbe_green';
    };
  }

  getTemperatureStyle(temperature: number) {
    if (temperature < 15) {
      return 'status_farbe_red';
    } else {
      return 'status_farbe_green';
    };
  }

  constructor(private websocketService: WebsocketService) { }

  //constructor(private statusService: StatusService) { }
  // getTemperature(): void {
  //   this.statusService.getTemperature()
  //   .subscribe(temperatureSensors => this.temperatureSensors = temperatureSensors);
  // }

  ngOnInit() {
    this.websocketService.temperatureSensors.subscribe(
      temperatureSensors => {this.temperatureSensors = temperatureSensors}
    )

    this.websocketService.devices.subscribe(
      devices => {this.devices = devices}
    )

    this.websocketService.dateOfMeasurements.subscribe(
      dateOfMeasurements => {this.date = dateOfMeasurements}
    )

    this.websocketService.timeOfMeasurements.subscribe(
      timeOfMeasurements => {this.time = timeOfMeasurements}
    )
  }

}


