import { Component, OnInit } from '@angular/core';
import { WebsocketService } from '../websocket.service';
import { StatisticsData, convertData2GoogleData } from '../statisticsData';

declare let google: any;
@Component({
  selector: 'app-statistics',
  templateUrl: './statistics.component.html',
  styleUrls: ['./statistics.component.css']
})
export class StatisticsComponent implements OnInit {

  constructor(private websocketService: WebsocketService) { }

  subscribeStatistics(staticsListener: string) {
    this.websocketService.subscribeStatistics(staticsListener);
  }
  
  ngOnInit() {
    this.websocketService.subscribeStatitics();
    this.websocketService.temperaturStatistics.subscribe(
      statistics => {
        console.log(statistics);
        showStatistics(statistics);
      }
    )
  }
}

function showStatistics(staticsData: StatisticsData) {
  var returnValue = convertData2GoogleData(staticsData);
  var dataColumns = returnValue.chartsData;
  var legendNames = returnValue.legendName;

  // Loading Visualization Bibliothek
  google.charts.load('current', {
    'packages': ['corechart'],
    'language': 'de'
  });

  // Seting callback to run when the Google Visualization is loaded
  google.charts.setOnLoadCallback(drawChart);


  function drawChart() {
    var data = new google.visualization.DataTable();
    data.addColumn('date', 'Date');
    for (let i = 0; i < legendNames.length; i++) {
      data.addColumn('number', legendNames[i]);
    }

    data.addRows(dataColumns);

    var options = {
      title: 'Temperaturverlauf [°C]',
      titleTextStyle: {
        color: 'black',
        fontSize: 30,
      },

      width: '100%',
      height: '400',

      legend: {
        position: 'bottom',
        textStyle: {
          fontSize: 11
        }
      },
      curveType: 'function',
      vAxes: {
        0: {
          title: 'Temperatur [C°]'
        }
      },
      hAxis: {
        viewWindow: {
          min: dataColumns[0][0],
          max: dataColumns[dataColumns.length - 1][0]
      }}
    };

    var chart = new google.visualization.LineChart(document.getElementById('linechart_material'));
    chart.draw(data, options);

  };
};