import { Component, OnInit } from '@angular/core';
import { DEVICESETTINGS } from '../dummy-deviceSettings';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {

  deviceSettings = DEVICESETTINGS;

  getLightStyle(lightOn: number, lightOff: number) {
    let lightStyle: tableStyle[] = [];
    let length: number;
    if (isNaN(lightOn)) {
        length = 24;
        lightStyle.push(new tableStyle("off", "tdGreenBlue", length));
    }
    else {
        length = lightOn - 1;
        lightStyle.push(new tableStyle("off", "tdGreenBlue", length));
        length = lightOff - lightOn;
        lightStyle.push(new tableStyle("on", "tdYellowGreen", length));
        length = 24 - lightOff + 1;        
        lightStyle.push(new tableStyle("off", "tdGreenBlue", length));
      }
      console.log(lightStyle);
    return lightStyle;
  }

  getTemperatureStyle (hourDayOn: number, hourDayOff: number, tempDay: number, tempNight: number){
    let temperatureStyle: tableStyle [] = [];
    let lenght: number;
    if (isNaN(hourDayOn)) { // Es gibt keinen Tag
      lenght = 24;
      temperatureStyle.push(new tableStyle(tempNight.toString(), "tdRed", lenght));
    }
    else {
      lenght = hourDayOn -1;
      temperatureStyle.push(new tableStyle(tempNight.toString(), "tdRed", lenght));
      lenght = hourDayOff - hourDayOn;
      temperatureStyle.push(new tableStyle(tempDay.toString(), "tdGreen", lenght));
      lenght = 24 - hourDayOff + 1;
      temperatureStyle.push(new tableStyle(tempNight.toString(), "tdRed", lenght));
    }
    return temperatureStyle;
  }

  createHours(maxHour: number) {
    let hours: number[] = [];
    for (let i = 1; i <= maxHour; i++) {
      hours.push(i);
    }
    return hours;
  }

  constructor() { }

  ngOnInit() {
  }

}

class tableStyle {
  string: string;
  style: string;
  colspan: number;
  constructor(myString: string, myStyle: string, myColspan: number) {
    this.string = myString;
    this.style = myStyle;
    this.colspan = myColspan;
  }
}